@media only screen and (max-width:768px) {
    .totall-product h2 {
        font-size: 28px;
    }

    .loop-grid.loop-list article {
        .post-thumb {
            min-height: 220px;
            min-width: 300px;
        }

        h3.post-title {
            font-size: 22px;
            margin-bottom: 30px !important;
        }

        .post-exerpt {
            display: none;
        }

        .entry-content-2.pl-50 {
            padding-left: 30px !important;
        }
    }

    .loop-big {
        h2.post-title {
            font-size: 32px;
        }
    }

    .header-style-1 .header-bottom-bg-color {
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.07);
    }

    .header-action-2 .header-action-icon-2:last-child {
        padding: 0;
    }

    .logo {
        &.logo-width-1 {
            margin-right: 0;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);

            a {
                img {
                    width: 150px;
                    min-width: 150px;
                }
            }
        }
    }

    .header-bottom {
        padding: 20px 0;
    }

    .header-action {
        .header-action-icon {
            margin-right: 15px;

            &.header-action-mrg-none {
                margin-right: 15px;
            }

            &.header-action-mrg-none2 {
                margin-right: 15px;
            }

            &>a {
                font-size: 20px;
            }
        }
    }

    .header-action-2 {
        .header-action-icon-2 {
            padding: 0 6px;

            &:last-child {
                padding: 0 0 0 6px;
            }

            &>a {
                color: #fff;
                fill: #fff;
                font-size: 22px;
                margin-right: 10px;
            }
        }
    }

    .cart-dropdown-wrap {
        width: 290px;
        right: -39px;

        ul {
            li {
                .shopping-cart-img {
                    -webkit-box-flex: 0;
                    -webkit-flex: 0 0 60px;
                    -ms-flex: 0 0 60px;
                    flex: 0 0 60px;
                    margin-right: 10px;
                }
            }
        }

        .shopping-cart-footer {
            .shopping-cart-button {
                a {
                    padding: 12px 22px 13px;
                }
            }
        }
    }

    .categories-dropdown-wrap {
        ul {
            li {
                a {
                    padding: 5px 12px;
                }
            }
        }
    }

    .search-popup-wrap {
        .search-popup-content {
            form {
                input {
                    width: 270px;
                    font-size: 25px;
                    padding: 21px 20px 12px 0;
                }
            }
        }
    }

    .mobile-header-wrapper-style {
        width: 380px;

        .mobile-header-wrapper-inner {
            .mobile-header-top {
                padding: 15px 30px 13px 30px;
                border-bottom: 1px solid $border-color;

                .mobile-header-logo a img {
                    width: 140px;
                }
            }

            .mobile-header-content-area {
                padding: 30px;
            }
        }
    }

    .header-height-1 {
        min-height: 65px;
    }

    .header-height-2 {
        min-height: 65px;
    }

    .home-slider.pt-50 {
        padding-top: 0 !important;
    }

    .hero-slider-content-2 {
        h1 {
            font-size: 32px;
            line-height: 1.2;
        }

        h2 {
            font-size: 30px;
            line-height: 1.2;
            margin-bottom: 10px;
        }

        h4 {
            font-size: 16px;
            margin: 0 0 10px;
        }

        .btn-default {
            color: #fff;
            background-color: $color-brand;
            border-radius: 50px;
            padding: 10px 22px;
            font-size: 14px;
        }

        p {
            font-size: 14px;
            line-height: 1.3;
        }
    }

    .banner-big {
        h4 {
            font-size: 12px;
        }

        h2 {
            font-size: 16px;
        }

        .btn {
            background-color: $color-brand;
            color: #fff;
            border-radius: 3px;
            font-size: 13px;
            padding: 10px 22px;
            border: 0;
        }
    }

    .home-slider {
        .slider-arrow {
            display: none;
        }

        .hero-slider-1 {
            height: 350px;

            &.style-2 {
                height: 400px;

                .hero-slider-content-2 {
                    padding-left: 0;
                    padding-top: 100px;
                }

                .single-slider-img {
                    img {
                        max-width: 400px;
                    }
                }

                .slider-1-height-2 {
                    height: 400px;
                    position: relative;
                }
            }
        }
    }

    .banner-img {
        float: left;

        &.f-none {
            float: none;
        }
    }

    .hero-slider-1.style-3 .slider-1-height-3 {
        height: 390px;
    }

    .hero-slider-1.style-3 .hero-slider-content-2 {
        padding-left: 20px;
    }

    .home-slide-cover .hero-slider-1.style-4 {
        .hero-slider-content-2 {
            padding-left: 0;

            h1 {
                font-size: 30px;
            }

            h2 {
                font-size: 14px;
            }

            h4 {
                font-size: 12px;
            }
        }
    }

    .header-style-3 {
        .header-bottom-bg-color {
            border-bottom: 2px solid $color-brand;
        }

        .main-nav {
            border: none !important
        }

        .sticky-bar {
            &.stick.sticky-blue-bg {
                background-color: $color-brand;
            }
        }
    }

    .header-style-4 {
        .main-nav {
            border: none !important
        }

        .sticky-bar {
            &.stick.sticky-blue-bg {
                background-color: $color-brand;
            }
        }
    }

    .font-xxl {
        font-size: 38px;
    }

    .single-page.pl-30,
    .single-page.pr-30 {
        padding-left: 0 !important;
        padding-right: 0 !important;
    }

    .download-app {
        margin-bottom: 0 !important;
    }

    .footer-mid {
        .logo {
            img {
                max-width: 150px;
            }
        }

        .widget-install-app,
        .widget-about {
            min-width: 205px;
        }

        .widget-about strong {
            display: none;
        }
    }

    .product-list {
        .product-cart-wrap {
            align-items: start;

            .product-img-action-wrap {
                max-width: 45%;
            }

            h2 {
                font-size: 24px;
            }

            p.mt-15 {
                display: none;
            }

            .product-rate-cover {
                margin-bottom: 20px;
            }
        }

    }

    .detail-info {
        padding: 0 !important;

        h2 {
            font-size: 30px;
        }

        .detail-extralink .detail-qty {
            padding: 11px 20px 11px 10px;
            max-width: 60px;
        }

        .product-extra-link2 .button.button-add-to-cart {
            padding: 0px 15px;

            i {
                display: none;
            }
        }
    }

    .shopping-summery {
        table tbody tr img {
            max-width: 80px;
            margin-right: 15px;
        }

        .form-check-label {
            display: none;
        }

        h6 {
            font-size: 14px;
        }

        td.pl-30 {
            padding-left: 0 !important;
        }

        button.btn {
            width: 120px;
            margin-left: 15px;
        }
    }

    .product-cart-wrap .product-img-action-wrap .product-action-1 {
        min-width: 111px;
    }

    .custom-modal .modal-dialog {
        max-width: 720px !important;
    }
}

