        padding-right: 0 !important;
    }

}

/*small phone*/
@media only screen and (max-width:375px) {

    .entry-meta .hit-count,
    .entry-meta.meta-2 .font-xs {
        display: none;
    }

    .deal {
        padding: 30px;
    }

    .custom-modal .modal-dialog .modal-content {
